import React, { useState, useEffect } from 'react';
import { Modal, message } from 'antd';
import ListProcessType from './list';
import FormProcess from './form';

import {
  addProcessType,
  getProcessTypes,
  addSubprocessType,
  delProcessTypes,
  delSubprocessType,
} from './../../services';

const ProcessType = () => {
  const [process, setProcess] = useState([]);
  const [loading, setLoading] = useState(false);
  const [view, setView] = useState('list');
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);

  useEffect(() => {
    fetch();
  }, []);

  const changeView = value => {
    if (value === 'add' || value === 'sub') {
      setVisible(true);
    }
    setView(value);
  };

  const handleCancel = () => {
    setVisible(false);
    setView('list');
  };

  const selectProcess = record => {
    setSelected(record);
  };

  const fetch = async () => {
    try {
      setLoading(true);
      const { data } = await getProcessTypes();
      setProcess(data);
      setLoading(false);
      handleCancel();
    } catch (error) {
      message.error(error.message);
      setLoading(false);
    }
  };

  const add = async (values, form) => {
    try {
      setLoading(true);
      if (view === 'add') await addProcessType(values);
      if (view === 'sub') {
        values['id'] = selected.id;
        await addSubprocessType(values);
      }
      form.resetFields();
      fetch();
      handleCancel();
      message.success('The action was successful');
    } catch (error) {
      message.error(error.message);
      setLoading(false);
    }
  };

  const removeProcess = async id => {
    try {
      setLoading(true);
      await delProcessTypes(id);
      fetch();
      message.success('The action was successful');
    } catch (error) {
      message.error(error.message);
      setLoading(false);
    }
  };

  const removeSubprocess = async id => {
    try {
      setLoading(true);
      await delSubprocessType(id);
      fetch();
      message.success('The action was successful');
    } catch (error) {
      message.error(error.message);
      setLoading(false);
    }
  };

  const getModalName = () => {
    if (view === 'sub') return 'Add Subprocess Type';
    if (view === 'add') return 'Add Process Type';
  };

  return (
    <React.Fragment>
      <ListProcessType
        changeView={changeView}
        loading={loading}
        process={process}
        selectProcess={selectProcess}
        removeProcess={removeProcess}
        removeSubprocess={removeSubprocess}
        num={Math.floor(Math.random() * 1000)}
      />
      <Modal title={getModalName()} visible={visible} onCancel={handleCancel} footer={false}>
        <FormProcess changeView={changeView} loading={loading} add={add} view={view} />
      </Modal>
    </React.Fragment>
  );
};

export default ProcessType;
