import React from 'react';
import { Table, Button, Row, Col, List } from 'antd';

const ListProcessType = ({
  changeView,
  loading,
  process,
  selectProcess,
  removeProcess,
  removeSubprocess,
  num,
}) => {
  const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Variable Name',
      dataIndex: 'variableName',
      key: 'variableName',
      render: value => {
        return <code>{value}</code>;
      },
    },
    {
      title: '',
      key: 'actions',
      width: 200,
      render: (value, record) => {
        return (
          <React.Fragment>
            <Button
              style={{
                float: 'right',
                marginLeft: 10,
              }}
              onClick={() => {
                removeProcess(record.id);
              }}
              icon="delete"
              size="small"
              type="danger"
            />

            <Button
              onClick={() => {
                changeView('sub');
                selectProcess(record);
              }}
              style={{
                float: 'right',
              }}
              type="primary"
              size="small"
            >
              Add Subprocess
            </Button>
          </React.Fragment>
        );
      },
    },
  ];

  const listSubprocess = record => {
    const data = record.SubprocessTypes;
    return (
      <List
        size="small"
        dataSource={data}
        renderItem={item => (
          <List.Item>
            <Row className="w-100">
              <Col span={12}>
                {item.name} <code>({item.variableName})</code>
              </Col>
              <Col span={12}>
                <Button
                  style={{
                    float: 'right',
                  }}
                  onClick={() => {
                    removeSubprocess(item.id);
                  }}
                  icon="delete"
                  size="small"
                  type="danger"
                />
              </Col>
            </Row>
          </List.Item>
        )}
      />
    );
  };

  return (
    <Row>
      <Col>
        <Row>
          <Col className="mb-2">
            {' '}
            <Button
              onClick={() => {
                changeView('add');
              }}
              type="primary"
              style={{
                float: 'right',
                marginBottom: 10,
              }}
            >
              Add Process
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>
            <Table
              key={num}
              loading={loading}
              size="small"
              dataSource={process}
              columns={columns}
              expandedRowRender={record => {
                return listSubprocess(record);
              }}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default ListProcessType;
