import React, { useState } from 'react';
import { Form, Input, Button, Row, Col } from 'antd';

const FormProcessCmp = ({ form, form: { getFieldDecorator }, add, loading }) => {
  const [variableName, setVariableName] = useState('');

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 16,
        offset: 8,
      },
    },
  };

  const handleSubmit = e => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        add(values, form);
      }
    });
  };

  const formatVariableName = e => {
    const { value } = e.target;
    // setVariableName(value);

    const name = value
      .toLowerCase()
      .split(' ')
      .join();
    setVariableName(name);
  };

  return (
    <Row>
      <Col xs={24}>
        <Form {...formItemLayout} onSubmit={handleSubmit}>
          <Form.Item label="Name">
            {getFieldDecorator('name', {
              rules: [
                {
                  required: true,
                  message: 'Please input the Name Process!',
                },
              ],
            })(<Input onChange={formatVariableName} />)}
          </Form.Item>
          <Form.Item label="Variable Name">
            {getFieldDecorator('variableName', {
              initialValue: variableName,
              rules: [
                {
                  required: true,
                  message: 'Please input the Variable Name!',
                },
              ],
            })(<Input />)}
          </Form.Item>

          <Form.Item {...tailFormItemLayout}>
            <Button loading={loading} type="primary" htmlType="submit">
              Add
            </Button>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
};

const FormProcess = Form.create()(FormProcessCmp);

export default FormProcess;
